.header {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--header-background);
}

.navList {
  display: flex;
  justify-content: center;
  align-items: center;
}
.navItem:first-child {
  margin-left: 620px;
}
.navItem:not(:last-child) {
  margin-right: 24px;
}
.navLink {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  line-height: 1.75;
  color: var(--hero-footer-text-color);
}
